import "../styles/App.css";
import { useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
// import IframeResizer from "iframe-resizer-react";

//

const NewsTemp = (Props) => {
  //
  // const RequestCurrentPageY = Props.RequestCurrentPageY;
  // let CurrentPageY = RequestCurrentPageY();
  const [IframeHeight, ChangeIframeHeight] = useState(200);
  //
  const [NewsURL, setNewsURL] = useState("");
  var CurrentSubfix = useLocation().pathname.replace("/Tin-moi/", "");

  //
  const NewsURLs =
    "https://script.google.com/macros/s/AKfycbxckev_aIb26ZDAvratbl9leg3GLnWsV98RYyV7NKtW4FZRWH24aWdnoQc03tTSi2ru/exec";

  const [TheNew, SetNews] = useState({});
  const [TheRelated, SetTheRelated] = useState([]);
  const [MailToURL, setMailToURL] = useState("");
  const [ShareURL, setShareURL] = useState("");

  //
  //
  const OldTitle = document.title;

  var News = {
    //
    SourceURL: "",
    URLSubfix: "",

    Date: "",
    Auth: "",
    AuthSubfix: "",
    SubfixURL: "",

    ViewCount: "12k",

    Thumbnail: "",

    Title: "",
    Chapeau: "",
  };

  //
  // var News = {
  //   //
  //   SourceURL:
  //     "https://news.vdfm.ga/Public/Chao-mung-den-voi-thoi-dai-cua-Metaverse/",
  //   URLSubfix: "Chao-mung-den-voi-thoi-dai-cua-Metaverse",

  //   Date: "27/10/2021",
  //   Auth: "Mai Anh",
  //   AuthSubfix: " , theo Style-Republik",
  //   SubfixURL:
  //     "https://style-republik.com/metaverse-va-thoi-trang-ky-thuat-so/",

  //   ViewCount: "12k",

  //   Thumbnail:
  //     "https://teknoloji.news/wp-content/uploads/2021/10/CROPPED-scaled-1.jpeg",

  //   Title: "Chào mừng đến với thời đại của Metaverse",
  //   Chapeau:
  //     "Metaverse là một phiên bản cấp tiến của Internet, một vũ trụ ảo nơi con người thật sự “tồn tại” trong đó. Đây chính là một ý tưởng đột phá, một môi trường ảo có rất nhiều tiềm năng phát triển",
  // };
  //
  var RelateCount = 1,
    TempRelate = [];
  const GetNews2 = () => {
    //

    fetch(NewsURLs)
      .then((NewsData) => NewsData.json())
      .then((NewsData) => {
        //
        Props.LoadCommit(false);
        Props.FullPageLoadCommit(false);

        //
        RelateCount = 1;
        TempRelate = [];

        //
        for (let index = 0; index < NewsData.length; index++) {
          if (CurrentSubfix != NewsData[index].Subfix && RelateCount <= 3) {
            RelateCount++;
            TempRelate.push(NewsData[index]);
            SetTheRelated(TempRelate);
          }

          if (CurrentSubfix == NewsData[index].Subfix) {
            //
            News.Thumbnail = NewsData[index].Thumbnail_Link;
            News.Chapeau = NewsData[index].Chapeau;
            News.Title = NewsData[index].Title;
            News.Auth = NewsData[index].Author;

            //
            News.Date = new Date(
              Date.parse(NewsData[index].Date)
            ).toLocaleDateString("vi-VN");

            if (NewsData[index].Ref != "") {
              News.AuthSubfix = " , theo " + NewsData[index].Ref;
              News.SubfixURL = NewsData[index].Ref_Link;
            }
            SetNews(News);

            //

            setMailToURL(
              "mailto:?&" +
                "subject=[VDFM.ga] " +
                News.Title +
                "&body=" +
                News.SourceURL +
                News.Chapeau
            );

            setShareURL(
              "https://www.facebook.com/" +
                "/sharer/sharer.php?kid_directed_site=0&sdk=joey&u=https%3A%2F%2Fvdfm.ga%2F%23%2FTin-moi%2F" +
                News.URLSubfix +
                "&display=popup&ref=plugin&src=share_button"
            );

            document.title = NewsData[index].Title + "| VDFM News";
          }
        }

        var NewsLoadTimer = setTimeout(() => {
          Props.LoadComplete(true);
          Props.FullPageLoadComplete(true);

          clearTimeout(NewsLoadTimer);
        }, 999);
      });
  };

  function ScrollReszieUpdate() {
    let body = document.body,
      main = document.getElementById("main_scroll"),
      ScrollReszieTimer = setTimeout(() => {
        body.style.height = Math.round(main.clientHeight) + "px";

        clearTimeout(ScrollReszieTimer);
      }, 1111);
    body.style.height = Math.round(main.clientHeight) + "px";
  }
  var ResizetimeOut;
  var NewsMessageEvent = window.addEventListener("message", (event) => {
    console.log(event.data);

    if ((event.data.code = "NewsTemplate")) {
      ChangeIframeHeight(event.data.innerHeight + 50);

      ResizetimeOut = setTimeout(() => {
        ChangeIframeHeight(event.data.innerHeight + 50);
        //
        ScrollReszieUpdate();
        clearTimeout(ResizetimeOut);
      }, 500);
      //
    }
    // if ((event.data.code = "404news")) {
    //   var FourOFourTimeOut = setTimeout(() => {
    //     window.location.href = "https://vdfm.ga/#/404";
    //     clearTimeout(FourOFourTimeOut);
    //   }, 1111);
    // }
    else {
      return;
    }
  });

  //
  function NewsTempLink() {
    let NavEls = document.querySelectorAll(".links li span"),
      NavElsText = document.querySelectorAll(".links li");

    NavEls.forEach((Element) => {
      Element.style = "";
    });
    NavElsText.forEach((Element) => {
      Element.style = "";
    });
  }
  NewsTempLink();

  const Ralate_Click = (event) => {
    console.log(event);
    console.log("revoked");
    window.scrollTo(0, 0);
    Props.LoadCommit(true);
    Props.LoadComplete(false);
    //
    CurrentSubfix = event;
    setNewsURL("https://news.vdfm.ga/Public/" + CurrentSubfix);

    GetNews2();
  };

  //
  //

  const NewsTempsEff = () => {
    Props.LoadCommit(true);
    Props.LoadComplete(false);
    GetNews2();

    ScrollReszieUpdate();
    window.scrollTo(0, 0);
    //
    setNewsURL("https://news.vdfm.ga/Public/" + CurrentSubfix);

    // setInterval(() => {
    //   CurrentPageY = RequestCurrentPageY();
    //   console.log(CurrentPageY);
    // }, 500);
    let DockedLayout = document.querySelector("#NewsLayout .DockedLayout"),
      DockItem = document.querySelector("#NewsLayout .DockedLayout .DockItem"),
      Footer = document.querySelector(".Footer");

    //DockItem
    var DockAnimationframe;
    const Doc_Trending_Content = () => {
      if (
        DockedLayout.getBoundingClientRect().top +
          DockedLayout.getBoundingClientRect().height / 2 -
          window.innerHeight / 2 <=
          0 &&
        Footer.getBoundingClientRect().top - window.innerHeight >= 0
      ) {
        DockItem.style.transform = `translateY(${-(
          DockedLayout.getBoundingClientRect().top +
          DockedLayout.getBoundingClientRect().height / 2 -
          window.innerHeight / 2
        )}px)`;
      }
      DockAnimationframe = window.requestAnimationFrame(Doc_Trending_Content);
    };
    DockAnimationframe = window.requestAnimationFrame(Doc_Trending_Content);

    //
    //
    // Cleaning Up data
    return () => {
      //
      console.log("Cleaned Up");
      document.title = OldTitle;
      window.cancelAnimationFrame(DockAnimationframe);
      window.removeEventListener("message", NewsMessageEvent);
      window.clearTimeout(ResizetimeOut);
    };
  };

  useEffect(NewsTempsEff, []);

  return (
    <main className="NewsTemp">
      <section className="Section" id="NewsHeaderTemp">
        <div
          className="NewsThumb"
          style={{ backgroundImage: `url("${TheNew.Thumbnail}")` }}
        ></div>
        <div className="NewsContents">
          <h2 id="NewsTitle">{TheNew.Title}</h2>
          <div className="Hor_line"></div>
          <div className="NewsMetaGroup">
            <h3>
              <span></span>
              {TheNew.Date}&nbsp;-&nbsp;từ&nbsp;<strong>{TheNew.Auth}</strong>
              <a href={TheNew.SubfixURL} target="_blank" rel="noreferrer">
                {TheNew.AuthSubfix}
              </a>
            </h3>
            <div className="MetaAndShare">
              <div id="ViewCount">{TheNew.ViewCount}</div>
              <a
                title="fb_share"
                href={ShareURL}
                target="_blank"
                rel="noreferrer"
              >
                <div id="FacebookShare"></div>
              </a>
              <a title="SendMail" href={MailToURL} rel="nofollow">
                <div id="Email"></div>
              </a>
            </div>
          </div>
        </div>
      </section>
      <section className="Section" id="NewsLayout">
        <div className="row">
          <div className="MainNews">
            <iframe
              src={NewsURL}
              frameBorder="0"
              // scrolling="no"
              loading="lazy"
              id="mainframe"
              height={IframeHeight + "px"}
            ></iframe>
            <div className="Related_news">
              <div className="Hor_line"></div>
              <h2>Tin khác</h2>
              <ul>
                {TheRelated.map((Relate) => (
                  <li key={Relate.Subfix}>
                    <a
                      title="share"
                      href={"#/Tin-moi/" + Relate.Subfix}
                      onClick={() => {
                        Ralate_Click(Relate.Subfix);
                      }}
                    >
                      {Relate.Title}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>
          <div className="DockedLayout">
            <div className="DockItem" id="item1">
              <div className="thumbnail_verr">
                <div className="thumbnail" id="Trending"></div>
                <a
                  href="https://issue.vdfm.ga/Releases/Ky-nguyen-Thoi-trang-Ky-thuat-so/"
                  // href="#/Tin-moi/Chao-mung-den-voi-thoi-dai-cua-Metaverse"
                  className="ItemHref"
                ></a>
              </div>
              <div className="overlay"></div>
              <div className="Trending_Content">
                <h2>Kỷ nguyên thời trang kỹ thuật số</h2>
                <h3>#2 - Hôm nay</h3>
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
};

export default NewsTemp;
