import "./styles/App.css";
import { useEffect, useState } from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

// Elements
import Nav from "./Elements/nav";
import HomePage from "./Elements/HomePage";
import InVietNam from "./Elements/InVietNam";
import DigitalFashion from "./Elements/DigitalFashion";
import Contact from "./Elements/Contact";
import About from "./Elements/About";
import WhatNew from "./Elements/WhatNew";
import VirtualInfluencer from "./Elements/VirtualInfluencer";
import Footer from "./Elements/Footer";
import FourOhFour from "./Elements/404er";
import ThankYou from "./Elements/ThankYou";
import NewsTemp from "./Elements/NewsTemp";
//
import FullPageLoader from "./Elements/FullpageLoading";
import InPageLoader from "./Elements/InPageLoader";
//
// Main Function
//
function App() {
  //
  var // sx = 0, // For scroll positions
    sy = 0;
  var // dx = sx, // For container positions And Force (Percentage 70% Recommended)
    dy = sy,
    Force = 70;

  //Scroll Reset
  const scrollLoad = () => {
    // Grab The Elements
    const body = document.body;
    const main = document.getElementById("main_scroll");

    // Constructing Values

    // Onpage Load And Refresh Events

    body.style.height = main.clientHeight + "px";

    // Resize Function

    function ScrollResize() {
      easeScroll();
      //
      let body = document.body,
        main = document.getElementById("main_scroll"),
        ScrollReszieTimer = setTimeout(() => {
          body.style.height = Math.round(main.clientHeight) + "px";
          easeScroll();

          clearTimeout(ScrollReszieTimer);
        }, 1111);
      body.style.height = Math.round(main.clientHeight) + "px";
    }
    window.addEventListener("resize", ScrollResize);
    window.addEventListener("mousedown", ScrollResize);
    window.addEventListener("touchstart", ScrollResize);

    //
    // Momentum Scrolling

    // Scroll Functions
    window.addEventListener("scroll", (e) => {
      easeScroll();

      // HeadersResponse();
    });

    function easeScroll() {
      // sx = window.pageXOffset;
      sy = window.pageYOffset;
    }

    window.requestAnimationFrame(renderScroll);

    function renderScroll() {
      //We calculate our container position by linear interpolation method
      // dx = li(dx, sx, Force / 1000);
      dy = li(dy, sy, Force / 1000);

      // dx = Math.floor(dx * 100) / 100;
      dy = Math.floor(dy * 100) / 100;

      main.style.transform = `translateY(-${dy}px)`;

      window.requestAnimationFrame(renderScroll);
    }

    function li(a, b, n) {
      return (1 - n) * a + n * b;
    }
    //

    //

    //
  };

  //

  //

  const [FullPageIsLoading, FullPageLoadCommit] = useState(true);
  const [FullPageIsComplete, FullPageLoadComplete] = useState(false);

  const [InPageIsLoading, InPageLoadCommit] = useState(false);
  const [InPageIsComplete, InPageLoadComplete] = useState(true);

  // const RequestPageY_Func = () => {
  //   // RequestPageY(dy);
  //   // console.log(dy);
  //   return dy;
  // };

  // Magazine Main Function
  const MagazineURL =
    "https://script.google.com/macros/s/AKfycbzax5cJ_C5DBKEKs4V632nF8x5ivVhMlkabjYtyeUiYW3Pfwj89BS-1OdBqXc8O9MB_/exec";

  // Prepare Values

  //

  const Preload = () => {
    scrollLoad();
    //
  };

  //Preload
  useEffect(Preload, []);

  return (
    <Router basename="" hashType="slash">
      <div className="App">
        <Nav LoadCommit={InPageLoadCommit} LoadComplete={InPageLoadComplete} />
        {/* Navigation Bar */}
        <div className="Stage" id="main_scroll">
          <Switch>
            {/* <Route path="/" exact component={HomePage} /> */}
            <Route path="/" exact>
              <HomePage
                InPageLoadCommit={InPageLoadCommit}
                InPageLoadComplete={InPageLoadComplete}
                FullPageLoadComplete={FullPageLoadComplete}
                FullPageLoadCommit={FullPageLoadCommit}
                MagazineURL={MagazineURL}
              />
            </Route>

            <Route path="/Tai-Viet-Nam" exact>
              <InVietNam
                InPageLoadCommit={InPageLoadCommit}
                InPageLoadComplete={InPageLoadComplete}
                FullPageLoadComplete={FullPageLoadComplete}
                FullPageLoadCommit={FullPageLoadCommit}
                MagazineURL={MagazineURL}
              />
            </Route>

            {/* <Route path="/Tai-Viet-Nam" exact component={InVietNam} /> */}
            {/* <Route path="/Thoi-trang-ao" exact component={DigitalFashion} /> */}
            {/*  */}
            <Route path="/Thoi-trang-ao" exact>
              <DigitalFashion
                InPageLoadCommit={InPageLoadCommit}
                InPageLoadComplete={InPageLoadComplete}
                FullPageLoadComplete={FullPageLoadComplete}
                FullPageLoadCommit={FullPageLoadCommit}
                MagazineURL={MagazineURL}
              />
            </Route>
            {/*  */}
            <Route path="/Lien-he" exact component={Contact} />
            <Route path="/Ve-chung-toi" exact component={About} />
            {/* <Route
              path="/Virtual-influencer"
              exact
              component={VirtualInfluencer}
            /> */}
            <Route path="/Virtual-influencer" exact>
              <VirtualInfluencer
                InPageLoadCommit={InPageLoadCommit}
                InPageLoadComplete={InPageLoadComplete}
                FullPageLoadComplete={FullPageLoadComplete}
                FullPageLoadCommit={FullPageLoadCommit}
                MagazineURL={MagazineURL}
              />
            </Route>

            <Route path="/Tin-moi" exact>
              <WhatNew
                LoadCommit={InPageLoadCommit}
                LoadComplete={InPageLoadComplete}
                FullPageLoadComplete={FullPageLoadComplete}
                FullPageLoadCommit={FullPageLoadCommit}
              />
            </Route>
            {/* <Route path="/Tin-moi/:id" component={NewsTemp} /> */}
            <Route path="/Tin-moi/:id">
              <NewsTemp
                LoadCommit={InPageLoadCommit}
                LoadComplete={InPageLoadComplete}
                FullPageLoadComplete={FullPageLoadComplete}
                FullPageLoadCommit={FullPageLoadCommit}
              />
            </Route>
            {/* Thank You Page */}
            <Route path="/ThankYou" exact component={ThankYou} />
            {/*  */}
            {/*  */}
            {/* 404 Page Not Found */}
            <Route path="*" component={FourOhFour} />
            <Route path="/404" component={FourOhFour} />
            {/* 404 Page Not Found */}
            {/*  */}
          </Switch>
          {/* Footer Zone */}
          <Footer />
          {/* Footer Zone */}
        </div>
      </div>
      {/* Loader Zone */}
      <InPageLoader Status={InPageIsLoading} IsComplete={InPageIsComplete} />
      <FullPageLoader
        Status={FullPageIsLoading}
        IsComplete={FullPageIsComplete}
      />
    </Router>
  );
}

export default App;
