  const GetNews = () => {
    fetch(NewsURL)
      .then((NewsData) => NewsData.json())
      .then((NewsData) => {
        Props.LoadCommit(false);
        Props.FullPageLoadCommit(false);

        tempitemArray = [];
        for (let index = 0; index < NumberOfItem; index++) {
          tempitemArray.push(NewsData[index]);
        }
        setItemArray(tempitemArray);
        let BackdropIMGTemp = tempitemArray.map((BackDrop) => {
          return BackDrop.Thumbnail_Link;
        });
        setBackDrop(BackdropIMGTemp);

        console.log(itemArray);
        //

        var NewsLoadTimer = setTimeout(() => {
          Props.LoadComplete(true);
          Props.FullPageLoadComplete(true);
          clearTimeout(NewsLoadTimer);
        }, 999);
        ScrollReszieUpdate();
      });
  };

