import "../styles/App.css";
import { useEffect, useState } from "react";

function DigitalFashion(Props) {
  var MagData = [];

  const [DFashion, setDFashion] = useState([]);
  async function GetMagazines() {
    console.log("MGZ Request");
    // return () => {
    var Mag = await fetch(Props.MagazineURL);
    MagData = await Mag.json();

    // return await MagData;
  }
  const DFinit = () => {
    GetMagazines().then(() => {
      // Set Up State

      let CurrentSortData = MagData.filter((item) => {
        //
        return item.Sort != ("Virtual Influencer" || "Tại Việt Nam");
      });

      console.log(CurrentSortData);
      setDFashion(CurrentSortData);
      // All set
      Props.InPageLoadCommit(false);
      Props.FullPageLoadCommit(false);

      var MagLoadTimer = setTimeout(() => {
        Props.FullPageLoadComplete(true);
        Props.InPageLoadComplete(true);
        clearTimeout(MagLoadTimer);
      }, 999);
    });
  };

  const [sortIndex, setSortIndex] = useState(0);
  //  window.scrollTo(0, 0);
  const ResetScrollHeight = () => {
    let body = document.body,
      main = document.getElementById("main_scroll"),
      ScrollReszieTimer = setTimeout(() => {
        body.style.height = Math.round(main.clientHeight) + "px";

        clearTimeout(ScrollReszieTimer);
      }, 1111);
    body.style.height = Math.round(main.clientHeight) + "px";
  };

  useEffect(ResetScrollHeight, [DFashion]);
  useEffect(ResetScrollHeight, [sortIndex]);

  const SortNames = [
    "Tất cả",
    "Những bước đầu của thời trang kỹ thuật số",
    "Thời trang kỹ thuật số có gì?",
    "Những triết lý nghệ thuật",
    "Công nghệ và thời trang số",
    "Con người và thời trang số",
  ];
  const ShortSortNames = [
    "Tất cả",
    "Khởi đầu",
    "Kỹ thuật số",
    "Nghệ thuật",
    "Công nghệ",
    "Con người",
  ];
  //
  const [IsListLayout, setLayout] = useState(true);
  //
  const SwapLayout = () => {
    setLayout(!IsListLayout);
  };
  //
  const [SortContent, ChangeSortIndex] = useState(SortNames[0]);
  const [SortOverlayState, SwapSortOverlaySlyle] = useState({
    isLeft: false,
    isRight: true,
  });
  const [SortSwaperValue, changeSortSwaperValue] = useState(0);
  const [SortPointerValue, changeSortPointerValue] = useState({
    widthValue: 0,
    leftValue: 0,
  });
  //

  //
  const SwapSortOnClick = (e) => {
    let NewSortIndex = e.target.getAttribute("sortindex"),
      NewSortNamesTimer,
      NewSortNamesTimerProgressCount = -1,
      OldSortNamesStringArr = SortContent.split(""),
      CurrentSortNamesStringArr = SortContent.split(""),
      CurrentSortNamesString = "",
      NewSortNamesStringArr = SortNames[NewSortIndex].split("");
    // console.log(e.target.getAttribute("sortindex"));
    //

    setSortIndex(NewSortIndex);
    let CurrentSortData = [];

    if (NewSortIndex === 0) {
      //
      CurrentSortData = MagData;
    } else if (NewSortIndex != 0) {
      //
      let CurShortSortNames = ShortSortNames[NewSortIndex];
      CurrentSortData = MagData.filter((item) => {
        return item.Sort == CurShortSortNames;
      });

      console.log(CurrentSortData);
      setDFashion(CurrentSortData);
    }

    clearInterval(NewSortNamesTimer);
    NewSortNamesTimer = setInterval(() => {
      // Invoking Progress
      NewSortNamesTimerProgressCount++;

      if (
        NewSortNamesTimerProgressCount >=
        OldSortNamesStringArr.length + NewSortNamesStringArr.length
      ) {
        CurrentSortNamesStringArr[
          NewSortNamesTimerProgressCount - OldSortNamesStringArr.length - 1
        ] =
          NewSortNamesStringArr[
            NewSortNamesTimerProgressCount - OldSortNamesStringArr.length - 1
          ];
        CurrentSortNamesString = CurrentSortNamesStringArr.join("");
        ChangeSortIndex(CurrentSortNamesString);
        ResetScrollHeight();
        clearInterval(NewSortNamesTimer); // Invoking Progress End
      } else if (
        NewSortNamesTimerProgressCount < OldSortNamesStringArr.length // Clear Old Contents Progress
      ) {
        CurrentSortNamesStringArr[
          OldSortNamesStringArr.length - NewSortNamesTimerProgressCount
        ] = "";
        CurrentSortNamesString = CurrentSortNamesStringArr.join("");
        ChangeSortIndex(CurrentSortNamesString);
      } else if (
        NewSortNamesTimerProgressCount >=
        OldSortNamesStringArr.length + 1 // Invoke New Contents Progress
      ) {
        CurrentSortNamesStringArr[
          NewSortNamesTimerProgressCount - OldSortNamesStringArr.length - 1
        ] =
          NewSortNamesStringArr[
            NewSortNamesTimerProgressCount - OldSortNamesStringArr.length - 1
          ];
        CurrentSortNamesString = CurrentSortNamesStringArr.join("");
        ChangeSortIndex(CurrentSortNamesString);
      }
    }, 5);

    // ChangeSortIndex(SortNames[NewSortIndex]);

    changeSortPointerValue({
      widthValue: e.target.offsetWidth,
      leftValue: e.target.offsetLeft,
    });
    // if (NewSortIndex in [0, 1, 2]) {
    //   SwapSortOverlaySlyle({});
    // }
    if (NewSortIndex >= 2 && NewSortIndex <= 4) {
      SwapSortOverlaySlyle({
        isLeft: true,
        isRight: true,
      });
      changeSortSwaperValue(e.target.offsetLeft - 160);
    } else if (NewSortIndex < 2) {
      SwapSortOverlaySlyle({
        isLeft: false,
        isRight: true,
      });
      changeSortSwaperValue(0);
    } else if (NewSortIndex > 4) {
      changeSortSwaperValue(e.target.offsetLeft - 200);

      SwapSortOverlaySlyle({
        isLeft: true,
        isRight: false,
      });
    }
  };
  const LoadFirstTagH5 = () => {
    let FirstTagH5Width = document.querySelector(".Sort_Swaper h5").offsetWidth;
    let FirstTagH5Left = document.querySelector(".Sort_Swaper h5").offsetLeft;
    changeSortPointerValue({
      widthValue: FirstTagH5Width,
      leftValue: FirstTagH5Left,
    });
  };
  //
  function DigitalFashEff() {
    Props.InPageLoadCommit(true);
    Props.InPageLoadComplete(false);
    DFinit();
    //
    ResetScrollHeight();

    LoadFirstTagH5();
    //
    function NavDigitalFash() {
      let NavEls = document.querySelectorAll(".links li span"),
        NavElsText = document.querySelectorAll(".links li"),
        DigitalFasLinkSpan = document.querySelector("#DigitalFasLink span"),
        DigitalFasLinkText = document.querySelector("#DigitalFasLink");

      NavEls.forEach((Element) => {
        Element.style = "";
      });
      NavElsText.forEach((Element) => {
        Element.style = "";
      });

      DigitalFasLinkSpan.style.height = `100%`;
      DigitalFasLinkText.style.color = `#151515`;
    }

    const Sort_Swaper = document.querySelectorAll(".Sort_Swaper h5");
    for (let index = 0; index < Sort_Swaper.length; index++) {
      const element = Sort_Swaper[index];

      element.addEventListener("click", SwapSortOnClick);
    }

    NavDigitalFash();
    //
  }

  useEffect(DigitalFashEff, []);
  //

  return (
    <main className="DigitalFashion">
      <section className="Section" id="Video_Header">
        <video
          src="https://vdfm.ga/assets/Videos/YUNHUI%20WANG%20VIRTUAL%20FASHION%20SHOW.mp4"
          autoPlay={true}
          loop={true}
          muted={true}
          poster="https://itk-assets.nyc3.cdn.digitaloceanspaces.com/2020/05/Screen-Shot-2020-05-27-at-12.37.23-PM.jpg"
        ></video>
        <h1>Thời trang ảo</h1>
        <h2>#Digital Fashion</h2>
      </section>
      <section className="Section" id="sec2">
        <div className="Sort_And_Layout">
          <div className="Sort_Swap">
            <div
              className="Sort_Swaper"
              style={{
                transform: `translateX(-${SortSwaperValue}px)`,
              }}
            >
              <h5 sortindex={0}>Tất cả</h5>
              <h5 sortindex={1}>Khởi đầu</h5>
              <h5 sortindex={2}>Kỹ thuật số</h5>
              <h5 sortindex={3}>Nghệ thuật</h5>
              <h5 sortindex={4}>Công nghệ</h5>
              <h5 sortindex={5}>Con người</h5>
              <div
                className="Active_Sort_Pointer"
                style={{
                  width: `${SortPointerValue.widthValue}px`,
                  left: `${SortPointerValue.leftValue}px`,
                }}
              ></div>
            </div>
            <div className="Sort_Swap_Overlay">
              <div
                className={
                  SortOverlayState.isLeft
                    ? "Left_Overlay Left_Overlay_Active"
                    : "Left_Overlay"
                }
              ></div>
              <div
                className={
                  SortOverlayState.isRight
                    ? "Right_Overlay Right_Overlay_Active"
                    : "Right_Overlay"
                }
              ></div>
            </div>
          </div>

          <div
            className={
              IsListLayout ? "LayOut_Swap" : "LayOut_Swap LayOut_Swap_Grid"
            }
            onClick={SwapLayout}
          >
            <div className="Overlay_Swap"></div>
            <div className="Swapp_button" id="OutBound"></div>
            <div className="Swapp_button" id="InBound"></div>
          </div>
        </div>
        <div className="Sort_Title">
          <div>
            <h3>{SortContent}</h3>
          </div>
        </div>
        <div className={IsListLayout ? "OutBoundLayout" : "InBoundLayout"}>
          {DFashion.map((item) => (
            <div className="Item" key={item.IssIndex}>
              <div className="thumbnail_square">
                <div
                  className="thumbnail"
                  style={{
                    backgroundImage: `url("${item.Thumbnail_Link}")`,
                  }}
                ></div>
              </div>
              <div className="contents">
                <h2 className="title">{item.Title}</h2>
                <h6 className="meta">
                  {"#" + item.IssIndex + " - " + item.TimeSpan}
                </h6>
                <h4 className="descriptions">{item.Chapeau}</h4>
              </div>
              <div className="view_count">
                {Math.floor(Math.random() * 15) + "k"}
              </div>
              <a
                href={"https://issue.vdfm.ga/Releases/" + item.Subfix}
                // href="#/Tin-moi/Chao-mung-den-voi-thoi-dai-cua-Metaverse"
                className="ItemHref"
              ></a>
            </div>
          ))}

          {/* <div className="Item" id="sticky1">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">Thách thức của thời trang Việt Nam</h2>
              <h6 className="meta">- Hôm nay</h6>
              <h4 className="descriptions">
                Nền kinh tế, ngân sách hạn hẹp; việc áp dụng công nghệ kỹ thuật
                còn chưa cao; người tiêu dùng chưa có đủ nhận thức và định kiến
                “khuôn mẫu” về thời trang - thực tế; và cả vấn đề bản quyền ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="Item" id="sticky2">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">Virtual Influencer bắt nguồn từ đâu?</h2>
              <h6 className="meta">- 3 ngày trước</h6>
              <h4 className="descriptions">
                Influencer là người có tầm ảnh hưởng, có khả năng ảnh hưởng đến
                quyết định, hành vi mua hàng của nhóm khách hàng mục tiêu. Những
                người này thường có một mức độ nổi tiếng nhất định, hoặc có kiến
                thức và địa vị đối với nhóm đối tượng khán giả của họ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="Item" id="sticky3">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">
                Gặp gỡ Virtual Influencers bậc nhất thế giới hiện nay
              </h2>
              <h6 className="meta">- 1 Tuần trước</h6>
              <h4 className="descriptions">
                Shudu Gram - Siêu mẫu kỹ thuật số đầu tiên
                <br /> Lil Miquela - đứa con tinh thần của Brud <br /> Bermuda{" "}
                <br />
                Blawko Imma
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="Item" id="sticky4">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">
                Tính bền vững của Thời trang kỹ thuật số
              </h2>
              <h6 className="meta">- 1 tháng trước</h6>
              <h4 className="descriptions">
                Dịch bệnh khiến cho khái niệm về “tính bền vững” đến gần với các
                nhà thiết kế nói riêng và công chúng nói chung. Tính bền vững -
                dừng lãng phí và tiết kiệm hơn - “thắt chặt trong khâu lựa chọn
                nguyên liệu” ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="Item even" id="sticky5">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">
                Xây dựng ngôi nhà thời trang kỹ thuật số
              </h2>
              <h6 className="meta">- 1 tháng trước</h6>
              <h4 className="descriptions">
                Những yếu tố cần thiết để thương hiệu Việt bắt kịp xu hướng thời
                trang thế giới Từ những thiếu sót của mình chỉ ra hướng đi cho
                các thương hiệu Việt Nam ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div> */}
          <div className="show_more_but">
            <h3>Hiện thêm</h3>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 780 350">
              <polyline className="arr-1" points="5 345 390 5 775 345" />
            </svg>
          </div>
        </div>
      </section>
    </main>
  );
}
export default DigitalFashion;
