import "../styles/App.css";

const FullPageLoader = (Props) => {
  //
  const InPageLoadProgress = () => {
    if (Props.Status == true && Props.IsComplete == false) {
      return "FullPageLoader";
    } else if (Props.Status == false && Props.IsComplete == true) {
      return "FullPageLoader Pageloader_FadingOut Pageloader_Hidden";
    } else if (Props.Status == false && Props.IsComplete == false) {
      return "FullPageLoader Pageloader_FadingOut";
    }
  };

  return (
    <div className={InPageLoadProgress()}>
      <div className="Loader">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 822 822">
          <circle className="loader-1" cx="411" cy="411" r="371" />
          <circle className="loader-2" cx="411" cy="411" r="371" />
        </svg>
      </div>
      <h3 className="Messages">
        Năm mới này, hãy có thật nhiều niềm vui, nhớ bảo vệ bản thân và tuân thủ
        thật tốt 5K, những độc giả của VDFM!
      </h3>
    </div>
  );
};
export default FullPageLoader;
