import "../styles/App.css";
import { Link } from "react-router-dom";
import { useEffect } from "react";
//

function ThankYou() {
  //

  function ThankYouLink() {
    let NavEls = document.querySelectorAll(".links li span"),
      NavElsText = document.querySelectorAll(".links li");

    NavEls.forEach((Element) => {
      Element.style = "";
    });
    NavElsText.forEach((Element) => {
      Element.style = "";
    });
  }
  ThankYouLink();
  //
  window.scrollTo(0, 0);
  //
  useEffect(() => {
    let body = document.body,
      main = document.getElementById("main_scroll"),
      ScrollReszieTimer = setTimeout(() => {
        body.style.height = Math.round(main.clientHeight) + "px";

        clearTimeout(ScrollReszieTimer);
      }, 1111);
    body.style.height = Math.round(main.clientHeight) + "px";
  }, []);

  return (
    <main className="ThankYou">
      <div id="overlay_matte"></div>
      <h1>Lời cám ơn</h1>
      <h3>Từ VDFM</h3>
      <div className="Hor_line"></div>
      <p>
        Hãy nhớ rằng: "Đóng góp từ độc giả <br /> Chính là nguồn cảm hứng của
        chúng tôi"
      </p>
      <div className="row">
        <Link to="/">
          <div className="Return_Home_Button">Trang Chủ</div>
        </Link>
      </div>
    </main>
  );
}

export default ThankYou;
